body {
  margin: 0;
  padding: 0;
  font-family: 'Arial', sans-serif;
  background: linear-gradient(135deg, #0f0c29, #302b63, #24243e);
  color: #ffffff;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header and Navigation */
header {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem 0;
}

nav {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #4a90e2;
}

.nav-links {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.nav-links a {
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #4a90e2;
}

.wallet-button {
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 5px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.3s ease;
}

.wallet-button:hover {
  background-color: #357abd;
}

.wallet-address {
  max-width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/* Main Content */
main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.hero {
  text-align: center;
  margin-bottom: 2rem;
}

h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
}

/* Presale Widget */
.presale-widget {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 30px;
  border-radius: 20px;
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
  backdrop-filter: blur(4px);
  border: 1px solid rgba(255, 255, 255, 0.18);
  max-width: 600px;
  width: 100%;
  box-sizing: border-box;
}

h2 {
  color: #ffffff;
  margin-bottom: 20px;
  font-size: 24px;
}

.countdown {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 15px;
}

.countdown-item {
  text-align: center;
  padding: 0 10px;
}

.countdown-value {
  font-size: 28px;
  font-weight: bold;
  color: #4a90e2;
  display: block;
  margin-bottom: 5px;
}

.countdown-label {
  font-size: 12px;
  text-transform: uppercase;
  display: block;
  color: rgba(255, 255, 255, 0.7);
}

.info-box {
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 20px;
}

.info-box p {
  color: #ffffff;
  margin: 5px 0;
  font-size: 14px;
}

.progress-bar {
  width: 100%;
  height: 10px;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 0;
}

.progress {
  height: 100%;
  background-color: #4a90e2;
  transition: width 0.5s ease-in-out;
}

.info-box.token-info {
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 15px;
  padding: 20px;
  margin-bottom: 20px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  padding: 5px 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.info-row:last-child {
  margin-bottom: 0;
  border-bottom: none;
}

.info-label {
  color: #ffffff;
  font-size: 14px;
  font-weight: 500;
}

.info-value {
  color: #4a90e2;
  font-size: 16px;
  font-weight: 700;
}

.balance-row {
  margin-top: 10px;
  padding-top: 10px;
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

.balance-value {
  color: #6ee7b7;
}

.payment-options {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.payment-options button {
  padding: 10px 20px;
  border: none;
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
  font-size: 14px;
}

.payment-options button.active {
  background-color: #4a90e2;
}

.purchase-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.1);
  color: #ffffff;
  font-size: 14px;
  box-sizing: border-box;
}

.purchase-form input::placeholder {
  color: rgba(255, 255, 255, 0.5);
}

.action-button {
  width: 100%;
  padding: 12px;
  background-color: #4a90e2;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.action-button:hover {
  background-color: #357abd;
}

.action-button:disabled {
  background-color: #999;
}

/* About Section */
.about {
  max-width: 800px;
  margin: 2rem auto;
  text-align: center;
}

/* Sectors Section */
.sectors {
  max-width: 1200px;
  margin: 2rem auto;
  text-align: center;
}

.sector-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
  margin-top: 1rem;
}

.sector {
  background-color: rgba(255, 255, 255, 0.1);
  padding: 1rem;
  border-radius: 10px;
  text-align: center;
}

.sector-icon {
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.sector p {
  font-size: 0.9rem;
  margin-top: 0.5rem;
}

/* Why Invest Section */
.why-invest {
  max-width: 800px;
  margin: 2rem auto;
  text-align: center;
}

.why-invest ul {
  text-align: left;
  list-style-type: none;
  padding-left: 0;
}

.why-invest li {
  margin-bottom: 0.5rem;
  padding-left: 1.5rem;
  position: relative;
}

.why-invest li::before {
  content: 'âœ“';
  color: #4a90e2;
  position: absolute;
  left: 0;
}

/* Footer */
footer {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 1rem 0;
  text-align: center;
}

/* Responsive Design */
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    gap: 1rem;
  }

  .sectors {
    flex-direction: column;
    align-items: center;
  }

  .sector-grid {
    grid-template-columns: repeat(2, 1fr);
  }

  .presale-widget {
    max-width: 95%;
    padding: 15px;
  }

  .countdown {
    flex-wrap: wrap;
  }

  .countdown-item {
    flex: 1 1 25%;
    margin-bottom: 10px;
  }

  main {
    padding: 0.75rem;
  }
}

@media (max-width: 480px) {
  .presale-widget {
    max-width: 98%;
    padding: 12px;
  }

  h1 {
    font-size: 24px;
  }

  h2 {
    font-size: 20px;
  }

  .info-box p {
    font-size: 12px;
  }

  .payment-options {
    flex-direction: column;
    gap: 10px;
  }

  .payment-options button {
    width: 100%;
    padding: 10px;
    font-size: 14px;
  }

  .purchase-form input {
    font-size: 14px;
    padding: 12px;
  }

  .action-button {
    font-size: 16px;
    padding: 12px;
  }

  .info-box.token-info {
    padding: 15px;
  }

  .info-label, .info-value {
    font-size: 13px;
  }

  .countdown-value {
    font-size: 24px;
  }

  .countdown-label {
    font-size: 10px;
  }

  main {
    padding: 0.5rem;
  }
}

@media (max-width: 360px) {
  .presale-widget {
    max-width: 99%;
    padding: 10px;
  }

  h1 {
    font-size: 20px;
  }

  .info-box {
    padding: 10px;
  }

  .payment-options button {
    padding: 8px;
    font-size: 12px;
  }

  .purchase-form input {
    padding: 10px;
  }

  .action-button {
    padding: 10px;
    font-size: 14px;
  }

  .info-box.token-info {
    padding: 10px;
  }

  main {
    padding: 0.25rem;
  }
}